import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../services/auth.service';
import { Router } from '@angular/router';
import { AdminInputService } from '../../../services/admin-input.service';
import { ToastrService } from 'ngx-toastr';
import { ValidateService } from '../../../services/validate.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  careers: any;
  universities: any;
  user: any;
  skills: any;
  career: String;
  careerskills: any;
  university: String;
  user_pro : any;
  showForm : Boolean = true;
  
  constructor(private auth: AuthService,
    private router: Router,
    private adminInput: AdminInputService,
    private validateService: ValidateService,
    private toastr: ToastrService,
    private location: Location) { }

    

  ngOnInit() {
    this.auth.getProfile().subscribe(profile => {
    this.user = profile.user;
    // getting all careers in data base
    this.adminInput.getCareer()
      .subscribe(resCareerData => this.careers = resCareerData);

    // getting all universities in data base
    this.adminInput.getUniversity()
      .subscribe(resUniversityData => this.universities = resUniversityData);
    },
  err =>{
    console.log(err);
    return false;
  });
  
  }
  


  
  onCareerSubmit(){
    
    this.showForm = false
    this.user_pro= {
      career: this.career,
      university: this.university,
      skills: this.skills,
      _id: this.user._id
    }
  
    if(!this.validateService.validateProfileInput(this.user_pro)){
      this.toastr.error('Error sending form', 'Please fill in the field', { timeOut: 3000 } );
      return false;
     }
    
    this.auth.addProfile(this.user_pro).subscribe( data => {
      if(data){
        this.toastr.success('Successfully.', 'The career has been added.', { timeOut: 3000 } );
        this.user = data;
        //location.reload();
      }
      else{
        this.toastr.error('Try again later.','Something went wrong.',  { timeOut: 3000 } );
        this.router.navigate(['/profile']);
      }
   })
  }

  onCareerChange(career:any){
    this.getSkills(career)
  }


  getSkills(car: any){
    // getting all skills for career in data base
  this.adminInput.getCareerSkills(car)
  .subscribe(resCareerSkillData => this.careerskills = resCareerSkillData);
  console.log(this.careerskills)
  }

}
