import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { map } from 'rxjs/operators';
import { JwtHelperService } from '@auth0/angular-jwt';
//import { tokenNotExpired } from 'angular2-jwt';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  
  authToken: any;
  user: any
  usertype: any;
  isAdmin: any;

  constructor(private http: Http, private jwtHelper: JwtHelperService) { }

  registerUser(user){
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.post('/api/user/register', JSON.stringify(user) , options).pipe(
      map((response: Response) => response.json()));
  }


  authenticateUser(user){
    console.log(localStorage.getItem('user'));
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.post('/api/user/authenticate',JSON.stringify(user) , options).pipe(
      map((response: Response) => response.json()));
  }

  addProfile(user: any) {
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.put('/api/user/profiles/' + user._id, JSON.stringify(user), options ).pipe(
      map((response: Response) => response.json()));
  }


  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token
  }

  loggedIn() {
    return !!localStorage.getItem('id_token');
    }

    userType(){
      return !!localStorage.getItem('type');
    }
  

  getProfile(){
    this.loadToken()
    let headers = new Headers({
      'Content-Type': 'application/json',
       'Authorization': this.authToken
      });
    let options = new RequestOptions({headers: headers});
    return this.http.get('/api/user/profile', options).pipe(
      map((response: Response) => response.json()));
  }

  


  

  storeUserData(token, user ){
    localStorage.setItem('id_token', token); 
    localStorage.setItem('user', JSON.stringify(user));
    if(user.type=="Admin"){
      localStorage.setItem('type', user.type);
    }
    this.authToken = token;
    this.user = user;
  }


  logout(){
    this.authToken = null;
    this.user = null;
    this.usertype = null;
    localStorage.clear();
  }

  ngOnInit() {
    const token: string = localStorage.getItem('token');
    this.jwtHelper.isTokenExpired(token);
  }
  

  

  
}
