import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AdminInputService {

  input: any

  constructor(private http: Http) { }

  addUniversity(input){
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.post('/api/user/university',JSON.stringify(input) , options).pipe(
      map((response: Response) => response.json()));
  }
  addCareer(input){
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.post('/api/user/career',JSON.stringify(input) , options).pipe(
      map((response: Response) => response.json()));
  }

  addSkill(input){
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.post('/api/user/skills',JSON.stringify(input) , options).pipe(
      map((response: Response) => response.json()));
  }

  getCareer(){
    return this.http.get('/api/user/career')
    .pipe(map((response: Response) => response.json()));
  }
  getSkill(){
    return this.http.get('/api/user/skills')
    .pipe(map((response: Response) => response.json()));
  }

  getUniversity(){
    return this.http.get('/api/user/university')
    .pipe(map((response: Response) => response.json()));
  }

  updateCareer(career: any) {
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.put('/api/user/career/' + career._id, JSON.stringify(career), options).pipe(
      map((response: Response) => response.json()));
  }

  deleteCareer(career: any) {
    return this.http.delete('/api/user/career/' + career._id).pipe(
      map((response: Response) => response.json()));
  }

  updateSkill(skill: any) {
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.put('/api/user/skills/' + skill._id, JSON.stringify(skill), options).pipe(
      map((response: Response) => response.json()));
  }

  deleteSkill(skill: any) {
    return this.http.delete('/api/user/skills/' + skill._id).pipe(
      map((response: Response) => response.json()));
  }

  updateUniversity(university: any) {
    let headers = new Headers({'Content-Type': 'application/json'});
    let options = new RequestOptions({headers: headers});
    return this.http.put('/api/user/university/' + university._id, JSON.stringify(university), options).pipe(
      map((response: Response) => response.json()));
  }

  deleteUniversity(university: any) {
    return this.http.delete('/api/user/university/' + university._id).pipe(
      map((response: Response) => response.json()));
  }


  getCareerSkills(career: any){
    return this.http.get('/api/user/skills/' + career)
    .pipe(map((response: Response) => response.json()));
  }

}
