import { Component, OnInit, EventEmitter } from '@angular/core';
import { Location } from '@angular/common';

@Component({
  selector: 'app-career-detail',
  templateUrl: './career-detail.component.html',
  styleUrls: ['./career-detail.component.css'],
  inputs: ['career'],
  outputs:['updateCareerEvent', 'deleteCareerEvent',]
})
export class CareerDetailComponent implements OnInit {
  private editTitle: boolean = false;
  private updateCareerEvent = new EventEmitter();;
  private deleteCareerEvent = new EventEmitter()

  constructor(
    private location: Location
  ) {}
  
  career: any; 
  
  ngOnInit() {
  }

  ngOnChanges(){
    this.editTitle = false;
  }

  onTitleClick(){
    this.editTitle = true;
  }

  updateCareer(){
    this.updateCareerEvent.emit(this.career)
  }

  deleteCareer(){
    this.deleteCareerEvent.emit(this.career)
  }

  goBack(): void {
    location.reload();
  }

}
