import { Component, OnInit, EventEmitter } from '@angular/core';
import { Location } from '@angular/common';

@Component({
  selector: 'app-university-detail',
  templateUrl: './university-detail.component.html',
  styleUrls: ['./university-detail.component.css'],
  inputs: ['university'],
  outputs:['updateUniversityEvent', 'deleteUniversityEvent',]
})
export class UniversityDetailComponent implements OnInit {

  private editUniTitle: boolean = false;
  private updateUniversityEvent = new EventEmitter();;
  private deleteUniversityEvent = new EventEmitter()

  constructor(
    private location: Location
  ) {}
  
  university: any; 
  
  ngOnInit() {
  }

  ngOnChanges(){
    this.editUniTitle = false;
  }

  onUniTitleClick(){
    this.editUniTitle = true;
  }

  updateUniversity(){
    this.updateUniversityEvent.emit(this.university)
  }

  deleteUniversity(){
    this.deleteUniversityEvent.emit(this.university)
  }

  goBack(): void {
    location.reload();
  }

}
