import { Component, OnInit, EventEmitter } from '@angular/core';
import { Location } from '@angular/common';

@Component({
  selector: 'app-skill-detail',
  templateUrl: './skill-detail.component.html',
  styleUrls: ['./skill-detail.component.css'],
  inputs: ['skill'],
  outputs:['updateSkillEvent', 'deleteSkillEvent',]
})
export class SkillDetailComponent implements OnInit {

  private editSkillTitle: boolean = false;
  private updateSkillEvent = new EventEmitter();;
  private deleteSkillEvent = new EventEmitter()

  constructor(
    private location: Location
  ) {}
  
  skill: any; 
  
  ngOnInit() {
  }

  ngOnChanges(){
    this.editSkillTitle = false;
  }

  onSkillTitleClick(){
    this.editSkillTitle = true;
  }

  updateSkill(){
    this.updateSkillEvent.emit(this.skill)
  }

  deleteSkill(){
    this.deleteSkillEvent.emit(this.skill)
  }

  goBack(): void {
    location.reload();
  }

}