import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdminComponent } from './admin.component';
import { ProductListComponent } from './components/product/list/list.component';
import { AddProductionComponent } from './components/product/add/add.component';
import { AdminLoginComponent } from './components/admin-login/admin-login.component';
import { AdminInputComponent } from './components/admin-input/admin-input.component';
import { AdminRegisterComponent } from './components/admin-register/admin-register.component';
import { AdminAuthGuard } from '../guards/admin-auth.guard.';
import { HomeComponent } from '../admin/components/home/home.component';


const routes: Routes = [
    { 
        path: 'admin', 
        component: AdminComponent,
        children: [
            { path: '', component: HomeComponent},
            { path: 'product/list', component: ProductListComponent },
            { path: 'product/add', component: AddProductionComponent },
            { path: 'login', component: AdminLoginComponent },
            { path: 'input', component: AdminInputComponent, canActivate:[AdminAuthGuard]},
            { path: 'register', component: AdminRegisterComponent },
            
        ]
    }
];

export const AdminRouting = RouterModule.forRoot(routes);