import { Component, OnInit, EventEmitter } from '@angular/core';
import { AdminInputService } from '../../../services/admin-input.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { ValidateService } from '../../../services/validate.service';

@Component({
  selector: 'app-admin-input',
  templateUrl: './admin-input.component.html',
  styleUrls: ['./admin-input.component.css'],
  
})
export class AdminInputComponent implements OnInit {
  careers: any;
  universities: any;
  skills: any;

  skillname: String;
  careerName: String;
  universityName: String;
  skillName: String;
  skillCareer: String;

  selectedCareer: any;
  selectedUniversity: any;
  selectedSkill: any;


  private hideNewCareer: boolean = true;
  private hideNewUniversity: boolean = true;
  private hideNewSkill: boolean = true;
  private showDetail: boolean = false;
  private showUniDetail: boolean = false;
  private showSkillDetail: boolean = false;
  private editTitle: boolean = false;
  private form: boolean = false;


  constructor(private adminInput: AdminInputService,
    private validateService: ValidateService,
    private toastr: ToastrService,
    private router: Router) { }

    ngOnInit() {
      // getting all careers in data base
      this.adminInput.getCareer()
        .subscribe(resCareerData => this.careers = resCareerData);

      // getting all universities in data base
      this.adminInput.getUniversity()
        .subscribe(resUniversityData => this.universities = resUniversityData);

      // getting all skills in data base
      this.adminInput.getSkill()
        .subscribe(resSkillData => this.skills = resSkillData);
    }

    ngOnChanges(){
      this.editTitle = false;
      this.form = false;
    }



    // Register university
    onUniversitySubmit(){
      const university = {
        name: this.universityName,
      };

      if(!this.validateService.validateInput(university.name)){
        this.toastr.error('Error sending form', 'Please fill in the field', { timeOut: 3000 } );
        return false;
      }
      
      this.adminInput.addUniversity(university).subscribe( data => {
      if(data.success){
        this.toastr.success('Successfully.', 'The university has been added.', { timeOut: 3000 } );
        this.hideNewUniversity = true;
        this.universities.push(data.msg); // Add to career list
        
      }
      else{
        this.toastr.error('Duplicated Try again.','Something went wrong.',  { timeOut: 3000 } );
        this.hideNewUniversity = true;
        this.router.navigate(['/admin/input']);
      }
    })
    }



    onUpdateCareerEvent(career) {
      
      this.adminInput.updateCareer(career)
      .subscribe(resUpdateCareer => career = resUpdateCareer);
      this.showDetail = false;
      this.toastr.success('Successfully.', 'The career has been updated.', { timeOut: 3000 } );
      //this.selectedVideo = null;
    }

    onDeleteCareerEvent(career:any) {
      let careerArray = this.careers;
      this.adminInput.deleteCareer(career)
      .subscribe(resDeleteCareer => {
        for(let i=0; i<careerArray.length; i++){
          if(careerArray[i]._id === career._id){
            careerArray.splice(i,1);
          }
        }
      });
      this.toastr.error('Successfully.', 'The career has been deleted.', { timeOut: 3000 } );
      this.showDetail = false;
    }



    onUpdateSkillEvent(skill) {
      
      this.adminInput.updateSkill(skill)
      .subscribe(resUpdateCareer => skill = resUpdateCareer);
      this.showSkillDetail = false;
      this.toastr.success('Successfully.', 'The skill has been updated.', { timeOut: 3000 } );
      //this.selectedVideo = null;
    }

    onDeleteSkillEvent(skill:any) {
      let skillArray = this.skills;
      this.adminInput.deleteSkill(skill)
      .subscribe(resDeleteSkill => {
        for(let i=0; i<skillArray.length; i++){
          if(skillArray[i]._id === skill._id){
            skillArray.splice(i,1);
          }
        }
      });
      this.toastr.error('Successfully.', 'The skill has been deleted.', { timeOut: 3000 } );
      this.showSkillDetail = false;
    }

    onUpdateUniversityEvent(university:any) {
      this.adminInput.updateUniversity(university)
      .subscribe(resUpdateUniversity => university = resUpdateUniversity);
      this.toastr.success('Successfully.', 'The university has been updated.', { timeOut: 3000 } );
      this.showUniDetail = false;
    }

    onDeleteUniversityEvent(university:any) {
      let universityArray = this.universities;
      this.adminInput.deleteUniversity(university)
      .subscribe(resDeleteUniversity => {
        for(let i=0; i<universityArray.length; i++){
          if(universityArray[i]._id === university._id){
            universityArray.splice(i,1);
          }
        }
      });
      this.toastr.error('Successfully.', 'The university has been deleted.', { timeOut: 3000 } );
      this.showUniDetail = false;
    }


    // Register Career
    onCareerSubmit(){
      const career = {
        name: this.careerName,
      };

      if(!this.validateService.validateInput(career)){
        this.toastr.error('Error sending form', 'Please fill in the field', { timeOut: 3000 } );
        return false;
      }
      
      this.adminInput.addCareer(career).subscribe( data => {
      if(data.success){
        this.toastr.success('Successfully.', 'The career has been added.', { timeOut: 3000 } );
        this.hideNewCareer = true;
        this.careers.push(data.msg); // Add to career list

        
      }
      else{
        this.toastr.error('Duplicated data. Try again.','Something went wrong.',  { timeOut: 3000 } );
        this.hideNewCareer = true;
        this.router.navigate(['/admin/input']);
      }
    })
    }

    onSkillSubmit(){
      const skill = {
        name: this.skillName,
        career: this.skillCareer
      };

      if(!this.validateService.validateSkillInput(skill)){
        this.toastr.error('Error sending form', 'Please fill in the field', { timeOut: 3000 } );
        return false;
      }
      
      this.adminInput.addSkill(skill).subscribe( data => {
      if(data.success){
        this.toastr.success('Successfully.', 'The skill has been added.', { timeOut: 3000 } );
        this.hideNewSkill = true;
        this.skills.push(data.msg); // Add to skill list
      }
      else{
        this.toastr.error('Duplicated Data. Try again.','Something went wrong.',  { timeOut: 3000 } );
        this.hideNewSkill = true;
        this.router.navigate(['/admin/input']);
      }
    })
    }

    newCareer() {
      this.hideNewCareer = false;
    }

    newUniversity() {
      this.hideNewUniversity = false;
    }

    newSkill() {
      this.hideNewSkill = false;
    }


    onTitleClick(){
      this.editTitle = true;
    }



    onSelectCareer(career: any){
      this.selectedCareer = career;
      this.hideNewCareer = true;
      // console.log(this.selectedCareer)
    }


    onSelectUniversity(university: any){
      this.selectedUniversity = university;
      this.hideNewUniversity = true;
      // console.log(this.selectedUniversity)
    }

    onSelectSkill(skill: any){
      this.selectedSkill = skill;
      this.hideNewSkill = true;
      // console.log(this.selectedUniversity)
    }

    goBack(){
      this.hideNewCareer= true;
    }

    goUniBack(){
      this.hideNewUniversity= true;
    }
    goSkillBack(){
      this.hideNewSkill= true;
    }


}
